<template>
  <div>
    <el-form>
      <!-- 上半部分 -->
      <el-form-item label="是否启用加密">
        <el-switch v-model="isEncrypted"></el-switch>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleBackup" style="width: 400px;">备份并下载</el-button>
      </el-form-item>

      <el-divider class="divider"/>

      <!-- 下半部分 -->
      <el-text class="mx-1">上传备份文件</el-text>
      <el-form-item label=""  style="margin-top: 10px">
        <el-upload
            class="upload-demo"
            drag
            :http-request="handleFileUpload"
            multiple
            :show-file-list="true"
            :file-list="fileList"
            style="width: 400px;"
        >
          <el-icon class="el-icon--upload">
            <upload-filled/>
          </el-icon>
          <div class="el-upload__text">
            Drop file here or <em>click to upload</em>
          </div>
          <template #tip>
            <div class="el-upload__tip">
              ⭐请上传备份压缩包文件
            </div>
          </template>
        </el-upload>
      </el-form-item>
      <el-form-item label="密钥" >
        <el-input v-model="encryptionKey" placeholder="输入密钥（可选）" clearable style="width: 400px;"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {UploadFilled} from '@element-plus/icons-vue'

export default {
  components: {
    UploadFilled
  },
  data() {
    return {
      isEncrypted: false,
      encryptionKey: '',
      fileList: []
    };
  },
  methods: {
    handleBackup() {
      // 处理备份逻辑
      console.log('备份中，是否加密：', this.isEncrypted);
    },
    // 自定义文件上传逻辑
    handleFileUpload({file}) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const arrayBuffer = e.target.result;
        if (arrayBuffer) {
          this.fileList.push(file);
        }
      };
      reader.readAsArrayBuffer(file); // 直接使用 file
    },
  }
};
</script>

<style scoped>
.upload-demo {
  border: 2px dashed #d9d9d9;
  border-radius: 6px;
  padding: 20px;
  text-align: center;
  cursor: pointer;
}

</style>
